import {cx} from '@emotion/css';
import {Box} from '@mui/material';
import CircularProgress from '@mui/material/CircularProgress';
import type {ReactElement} from 'react';
import React, {useCallback, useEffect, useRef, useState} from 'react';
import {CSSTransition} from 'react-transition-group';

import {PracticeRoundBoard} from './PracticeRoundBoard';
import {styles} from './styles';
import {playAudio} from '../../utility/playAudio';
import {LessonType} from '../Levels/types';
import type {Round} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';

interface PracticeRoundProps {
    round: Round;
    totalRounds: number;
    onAnswer: (correct: boolean, givenAnswer: number, time: Date) => void;
    onNext: () => void;
    tutorial: boolean;
}

export function PracticeRound({
    totalRounds,
    round,
    onAnswer,
    onNext,
    tutorial,
}: PracticeRoundProps): ReactElement {
    const [answering, setAnsweringImpl] = useState<boolean>(false);
    const answeringRef = useRef<boolean>(false);
    const [answered, setAnswered] = useState<boolean>(false);
    const loadingIndicatorRef = useRef(null);

    const tasks = round.tasks;

    useEffect(() => {
        setAnswered(false);

        playAudio('sounds/notification.mp3');

        if (round.order === totalRounds - 5) {
            setTimeout(() => playAudio('sounds/5_rounds_remaining.mp3'), 400);
        }
        if (round.order === totalRounds - 1) {
            setTimeout(() => playAudio('sounds/last_round.mp3'), 400);
        }
    }, [round, totalRounds]);

    const setAnswering = (value: boolean) => {
        answeringRef.current = value;
        setAnsweringImpl(value);
    };

    const submitAnswer = useCallback(
        async (answer: number) => {
            if (answer >= tasks.length) {
                return;
            }
            setAnswering(true);
            const answerTime = new Date();
            console.log('Answer time:', answerTime);

            const correct = answer === round.answerIndex;

            try {
                await playAudio(tasks[answer].audioFile);

                if (round.lessonType === LessonType.Pick1) {
                    await playAudio(
                        round.tasks[round.answerIndex].rightAnswerAudioFile,
                    );
                }
            } catch (e) {
                console.error('Error playing audio:', e);
            }

            onAnswer(correct, answer, answerTime);

            setAnswered(true);
            setAnswering(false);
        },
        [round, onAnswer, tasks],
    );

    const buttonAnswer = useCallback(
        async (answer: number) => {
            await submitAnswer(answer);
        },
        [submitAnswer],
    );

    const keyboardAnswer = useCallback(
        async (answer: number) => {
            if (answeringRef.current || answered) {
                return;
            }
            await submitAnswer(answer);
        },
        [submitAnswer, answered],
    );

    // eslint-disable-next-line react-hooks/exhaustive-deps
    // const voiceAnswer = useCallback(
    //     debounce(async (vAnswer: string) => {
    //         if (answeringRef.current || answered) {
    //             return;
    //         }
    //
    //         const answer = tasks.findIndex(c => c.voice === vAnswer);
    //         console.log('Voice answer:', vAnswer, answer);
    //         if (answer < 0) {
    //             return;
    //         }
    //
    //         await submitAnswer(answer);
    //     }, 300),
    //     [submitAnswer, answered],
    // );

    // useWebSpeechApi(!answering, voiceAnswer);

    const onKeyDown = useCallback(
        async (key: string) => {
            console.debug('Pressed key:', key);
            if (answered) {
                if (key === 'Enter' || key === ' ') {
                    onNext();
                }
                return;
            }

            const parsed = Number.parseInt(key);
            if (Number.isNaN(parsed)) {
                return;
            }

            await keyboardAnswer(parsed - 1);
        },
        [answered, onNext, keyboardAnswer],
    );

    const nextRound = useCallback(() => {
        if (answered) {
            onNext();
        }
    }, [answered, onNext]);

    useEventListener('keydown', event =>
        onKeyDown((event as KeyboardEvent).key),
    );

    return (
        <Box
            sx={[styles.fullScreen, {touchAction: 'none'}]}
            onClick={nextRound}
        >
            <PracticeRoundBoard
                tasks={tasks}
                hideControls={answering || answered}
                onAnswer={buttonAnswer}
                round={round}
                totalRounds={totalRounds}
                tutorial={tutorial}
            />
            <CSSTransition
                in={answering}
                classNames="fade"
                nodeRef={loadingIndicatorRef}
                unmountOnExit
                addEndListener={() => {}}
            >
                <Box
                    className={cx(styles.centered, styles.noInteraction)}
                    ref={loadingIndicatorRef}
                >
                    <Box
                        className={cx(
                            styles.answeringProgressContainer,
                            styles.withBackground,
                        )}
                    >
                        <CircularProgress
                            color="inherit"
                            sx={{display: 'block'}}
                        />
                    </Box>
                </Box>
            </CSSTransition>
            {answered && (
                <Box className={`${styles.bottom} ${styles.withBackground}`}>
                    Press ENTER/SPACE or click anywhere to continue ...
                </Box>
            )}
        </Box>
    );
}
