import type {LessonType} from '../Levels/types';

export type Round = Pick1Round;

export interface BaseRound {
    order: number;
}

export interface Pick1Round extends BaseRound {
    lessonType: LessonType.Pick1 | LessonType.Pick1SplitScreen;
    tasks: Task[];
    answerIndex: number;
}

export enum TaskType {
    Color,
    Shape,
    Character,
    Word,

}

export interface Task {
    id: string;
    label: string;
    type: TaskType;
    value: string;
    audioFile: string;
    rightAnswerAudioFile: string;
}
