import {cx} from '@emotion/css';
import {ArrowBack} from '@mui/icons-material';
import {Box, Button, IconButton, Stack, Typography} from '@mui/material';
import type {PropsWithChildren, ReactElement} from 'react';
import React, {useCallback, useEffect, useState} from 'react';

import {PracticeRoundBoard} from './PracticeRoundBoard';
import {styles} from './styles';
import {NarrowLayout} from '../Common/NarrowLayout';
import type {Lesson} from '../Levels/types';
import {LessonType} from '../Levels/types';
import type {Task} from '../Pages/assignments';
import {TaskType} from '../Pages/assignments';
import {useEventListener} from '../Pages/useEventListener';
import {useAnalytics} from 'use-analytics';

interface SessionIntroProps {
    onStart: (tutorial: boolean) => void;
    onBack: () => void;
    lesson: Lesson;
}

type IntroProps = {
    tasks: Task[];
};

function ScreenPreview({children}: PropsWithChildren<{}>): ReactElement {
    return (
        <Box
            position="relative"
            height="200px"
            sx={{border: '1px solid white', borderRadius: '2px'}}
        >
            {children}
        </Box>
    );
}

function Pick1Intro({tasks}: IntroProps): ReactElement {
    return (
        <Stack gap={2} mb={4}>
            {tasks.map((t, i) => (
                <ScreenPreview key={t.id}>
                    <PracticeRoundBoard
                        tasks={tasks}
                        hideControls={true}
                        onAnswer={() => Promise.resolve()}
                        round={{
                            answerIndex: i,
                            tasks: tasks,
                            lessonType: LessonType.Pick1,
                            order: 0,
                        }}
                        totalRounds={0}
                        tutorial={true}
                    />
                </ScreenPreview>
            ))}
        </Stack>
    );
}

/** This is a bit of a hack to get the same layout as the real game. */
function Pick1SplitScreenIntro({tasks}: IntroProps): ReactElement {
    return (
        <>
            <Typography paragraph gutterBottom>
                The goal here is to find the{' '}
                {tasks[0].type === TaskType.Color ? 'color' : 'shape'} "
                {tasks[0].label}" on either side of the screen.
            </Typography>

            <Stack gap={2} mb={4}>
                <ScreenPreview>
                    <PracticeRoundBoard
                        tasks={[tasks[0], tasks[1]]}
                        hideControls={true}
                        onAnswer={() => Promise.resolve()}
                        round={{
                            answerIndex: 0,
                            tasks: tasks,
                            lessonType: LessonType.Pick1SplitScreen,
                            order: 0,
                        }}
                        totalRounds={0}
                        tutorial={true}
                    />
                </ScreenPreview>
                <ScreenPreview>
                    <PracticeRoundBoard
                        tasks={[tasks[2], tasks[0]]}
                        hideControls={true}
                        onAnswer={() => Promise.resolve()}
                        round={{
                            answerIndex: 1,
                            tasks: tasks,
                            lessonType: LessonType.Pick1SplitScreen,
                            order: 0,
                        }}
                        totalRounds={0}
                        tutorial={true}
                    />
                </ScreenPreview>
            </Stack>
        </>
    );
}

/**
 * This shows a preview of the lesson before starting it.
 *
 * @param onStart
 * @param onBack
 * @param lesson
 */
export function SessionIntro({
    onStart,
    onBack,
    lesson,
}: SessionIntroProps): ReactElement {
    const {track} = useAnalytics();

    const [blindfolding, setBlindfolding] = useState(false);

    const handler = useCallback(() => onStart(true), [onStart]);
    useEventListener('keydown', blindfolding ? handler : undefined);

    if (blindfolding) {
        return (
            <Box
                sx={[styles.fullScreen, {p: 2}]}
                display="flex"
                justifyContent="center"
                alignItems="center"
                onClick={() => onStart(false)}
            >
                <Box display="inline-block">
                    <Typography paragraph>
                        Put on your blindfold now.
                    </Typography>
                    <Typography paragraph>
                        Tap anywhere or press any key to start.
                    </Typography>
                </Box>
            </Box>
        );
    }

    const {tasks} = lesson;

    const handleStartTutorial = async () => {
        onStart(true);

        await track('session-start', {
            tutorial: true,
            lesson: lesson.id,
        });
    };
    const handleStartPractice = async () => {
        setBlindfolding(true);

        await track('session-start', {
            tutorial: false,
            lesson: lesson.id,
        });
    };

    return (
        <Box
            className={cx(styles.withDarkerBackground)}
            p={2}
            minHeight="100vh"
        >
            <NarrowLayout>
                <IconButton onClick={onBack}>
                    <ArrowBack />
                </IconButton>

                <Typography variant="h2" gutterBottom>
                    Get ready!
                </Typography>

                <Typography paragraph gutterBottom>
                    The following screen previews illustrate how you should
                    answer each task.
                </Typography>

                {lesson.type === LessonType.Pick1 && (
                    <Pick1Intro tasks={tasks} />
                )}
                {lesson.type === LessonType.Pick1SplitScreen && (
                    <Pick1SplitScreenIntro tasks={tasks} />
                )}

                <Stack gap={2} justifyContent="space-between" direction="row">
                    <Button
                        variant="outlined"
                        fullWidth
                        size="large"
                        onClick={handleStartTutorial}
                    >
                        First I want to see how this works
                    </Button>
                    <Button
                        variant="contained"
                        fullWidth
                        size="large"
                        onClick={handleStartPractice}
                    >
                        I'm ready to put my blindfold on
                    </Button>
                </Stack>
            </NarrowLayout>
        </Box>
    );
}
