import React, {useMemo} from 'react';
import {useParams} from 'react-router-dom';

import {LESSONS} from '../../../../sessions/lessons';
import {ALL_TASKS_BY_ID} from '../../../../sessions/tasks';
import {shuffle} from '../../../../utility/shuffle';
import {useQueryParam} from '../../../../utility/useQueryParam';
import type {
    Lesson,
    LessonConfigPick1,
    LessonPick1,
} from '../../../Levels/types';
import {LessonType} from '../../../Levels/types';
import type {PracticeSessionData} from '../../../PracticeSession/PracticeSession';
import {PracticeSession} from '../../../PracticeSession/PracticeSession';
import type {Round, Task} from '../../assignments';

const TOTAL_ROUNDS = 10;

function getTasks(taskIds: string[]): Task[] {
    return taskIds.map(t => ALL_TASKS_BY_ID[t]).filter(t => !!t) as Task[];
}

function getRounds(lesson: Lesson, numberOfRounds: number): Round[] {
    const rounds: Round[] = [];
    for (let i = 0; i < numberOfRounds; i++) {
        if (lesson.type == LessonType.Pick1) {
            const randomPick = Math.trunc(Math.random() * lesson.tasks.length);

            rounds.push({
                lessonType: lesson.type,
                order: i,
                answerIndex: randomPick,
                tasks: lesson.tasks,
            });
        } else if (lesson.type == LessonType.Pick1SplitScreen) {
            const rightAnswerTask = lesson.tasks[0];
            const shuffledRest = shuffle([...lesson.tasks.slice(1)]).slice(
                0,
                lesson.config.split - 1,
            );
            const shuffledTasks = shuffle([rightAnswerTask, ...shuffledRest]);

            const answerIndex = shuffledTasks.findIndex(
                e => e.id === lesson.tasks[0].id,
            );

            rounds.push({
                lessonType: lesson.type,
                order: i,
                answerIndex,
                tasks: shuffledTasks,
            });
        }
    }
    return rounds;
}

export function usePracticeSession(lessonId?: string): PracticeSessionData {
    const taskIdsParam = useQueryParam('tasks');

    return useMemo<PracticeSessionData>(() => {
        if (taskIdsParam) {
            const taskIds = taskIdsParam?.split(',');
            const lesson = {
                type: LessonType.Pick1,
                config: {
                    type: LessonType.Pick1,
                    tasks: taskIds,
                } as LessonConfigPick1,
                tasks: getTasks(taskIds),
            } as LessonPick1;
            return {
                lesson: lesson,
                rounds: getRounds(lesson, TOTAL_ROUNDS),
            };
        }

        if (lessonId) {
            const lesson = {
                id: lessonId,
                type: LESSONS[lessonId].type,
                config: LESSONS[lessonId],
                tasks: getTasks(LESSONS[lessonId].tasks),
            } as Lesson;
            return {
                lesson: lesson,
                rounds: getRounds(lesson, TOTAL_ROUNDS),
            };
        }

        throw new Error('Neither taskIds nor lessonId was defined!');
    }, [taskIdsParam, lessonId]);
}

export function BlindfoldSessionPage() {
    const {id} = useParams<{id: string}>();
    const session = usePracticeSession(id);

    return <PracticeSession session={session} />;
}
