import React from 'react';
import ReactDOM from 'react-dom/client';

import './index.css';

import {
    createHashRouter,
    createRoutesFromElements,
    Route,
    RouterProvider,
    Routes,
} from 'react-router-dom';

import {App} from './App';
import {PageLayout} from './Components/Common/PageLayout';
import {Introduction} from './Components/Levels/Introduction/Introduction';
import {LevelRoutes} from './Components/Levels/LevelRoutes';
import {BlindfoldSessionPage} from './Components/Pages/Blindfold/BlindfoldSessionPage/BlindfoldSessionPage';
import {HomePage} from './Components/Pages/HomePage/HomePage';
import {LoginPage} from './Components/Pages/LoginPage/LoginPage';
import reportWebVitals from './reportWebVitals';
// import * as serviceWorkerRegistration from './serviceWorkerRegistration';

const router = createHashRouter(
    createRoutesFromElements(
        <Route path="/" element={<App />}>
            <Route element={<PageLayout />}>
                <Route path="/" element={<HomePage />} />
                <Route path="/introduction" element={<Introduction />} />
                <Route path="/level/*" element={<LevelRoutes />} />
            </Route>
            <Route path="/session/:id" element={<BlindfoldSessionPage />} />
            <Route path="/login" element={<LoginPage />} />

            {/* <Route path="/signup">*/}
            {/*    <SignUpPage />*/}
            {/* </Route>*/}
        </Route>,
    ),
);

ReactDOM.createRoot(document.getElementById('root') as HTMLElement).render(
    <RouterProvider router={router} />,
);

