import {CssBaseline, ThemeProvider} from '@mui/material';
import React, {useEffect} from 'react';
import {
    Outlet,
    Route,
    Routes,
    ScrollRestoration,
    useLocation,
} from 'react-router-dom';
import {AnalyticsProvider, useAnalytics} from 'use-analytics';

import './App.css';

import {PageLayout} from './Components/Common/PageLayout';
import {Introduction} from './Components/Levels/Introduction/Introduction';
import {LevelRoutes} from './Components/Levels/LevelRoutes';
import {BlindfoldSessionPage} from './Components/Pages/Blindfold/BlindfoldSessionPage/BlindfoldSessionPage';
import {HomePage} from './Components/Pages/HomePage/HomePage';
import {LoginPage} from './Components/Pages/LoginPage/LoginPage';
import {AppContextProvider} from './contexts/AppContext';
import {theme} from './styles/theme';
import {ProvideAuth} from './useAuth';

import {Analytics} from 'analytics';
import googleAnalytics from '@analytics/google-analytics';

/* Initialize analytics & load plugins */
const analytics = Analytics({
    app: 'awesome-app',
    plugins: [
        googleAnalytics({
            measurementIds: ['G-DGV6F6KJ95'],
        }),
    ],
});

export function App() {
    return (
        <AnalyticsProvider instance={analytics}>
            <ThemeProvider theme={theme}>
                <CssBaseline />
                <ProvideAuth>
                    <AppContextProvider>
                        <ScrollRestoration />
                        <AppTracking />
                    </AppContextProvider>
                </ProvideAuth>
            </ThemeProvider>
        </AnalyticsProvider>
    );
}

export function AppTracking() {
    const location = useLocation();
    const {track, page, identify} = useAnalytics();
    useEffect(() => {
        track('pageView', {path: location.pathname});
        page();
        // identify('user-id', {
        //     name: 'John Doe',
        //     email: '',
        // });
    }, [location.pathname, track, page, identify]);

    return <Outlet />;
}

export function Router() {
    return (
        <Routes>
            <Route element={<PageLayout />}>
                <Route path="/" element={<HomePage />} />
                <Route path="/introduction" element={<Introduction />} />
                <Route path="/level/*" element={<LevelRoutes />} />
            </Route>
            <Route path="/session/:id" element={<BlindfoldSessionPage />} />
            <Route path="/login" element={<LoginPage />} />

            {/* <Route path="/signup">*/}
            {/*    <SignUpPage />*/}
            {/* </Route>*/}
        </Routes>
    );
}

