import {Box, ButtonBase, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React from 'react';

import {styles} from './styles';
import type {ShapeType} from '../Common/Shape';
import {Shape} from '../Common/Shape';
import {LessonType} from '../Levels/types';
import type {Round, Task} from '../Pages/assignments';
import {TaskType} from '../Pages/assignments';

interface PracticeRoundBoardProps {
    tasks: Task[];
    hideControls: boolean;
    onAnswer: (answer: number) => Promise<void>;
    round: Round;
    totalRounds: number;
    tutorial: boolean;
}

export function PracticeRoundBoard({
    hideControls,
    onAnswer,
    round,
    tasks,
    totalRounds,
    tutorial,
}: PracticeRoundBoardProps): ReactElement {
    const rightAnswer = round.tasks[round.answerIndex];
    return (
        <>
            <Stack
                sx={{
                    ...sxStyles.fullCover,
                    backgroundColor:
                        round.lessonType == LessonType.Pick1 &&
                        rightAnswer.type === TaskType.Color
                            ? rightAnswer.value
                            : 'black',
                }}
                direction="row"
                flex={1}
                minWidth="45%"
                flexWrap="wrap"
            >
                {round.lessonType == LessonType.Pick1 &&
                    rightAnswer.type === TaskType.Shape && (
                        <Box
                            display="flex"
                            sx={[sxStyles.fullCover, {p: '1rem'}]}
                            justifyContent="center"
                        >
                            <Shape
                                shape={rightAnswer.id as ShapeType}
                                color="white"
                            />
                        </Box>
                    )}
                {tasks.map((task, i) => (
                    <Box
                        flex={1}
                        sx={sxStyles.optionButton}
                        position="relative"
                        key={task.id}
                    >
                        <ButtonBase
                            disabled={hideControls}
                            onClick={() => onAnswer(i)}
                            aria-label={`Option ${i + 1}`}
                            sx={{
                                ...sxStyles.fullCover,
                                backgroundColor:
                                    round.lessonType == LessonType.Pick1
                                        ? 'transparent'
                                        : task.value,
                            }}
                        >
                            {round.lessonType == LessonType.Pick1SplitScreen &&
                                rightAnswer.type === TaskType.Shape && (
                                    <Box
                                        display="flex"
                                        sx={[sxStyles.fullCover, {p: '1rem'}]}
                                        justifyContent="center"
                                    >
                                        <Shape
                                            shape={task.id as ShapeType}
                                            color="white"
                                        />
                                    </Box>
                                )}

                            {tutorial && i == round.answerIndex ? (
                                <Box sx={sxStyles.hint}>
                                    Click here or press [{i + 1}]
                                </Box>
                            ) : null}
                        </ButtonBase>
                        {!hideControls && (
                            <Box className={styles.optionLabel}>
                                {task.label} [{i + 1}]
                            </Box>
                        )}
                    </Box>
                ))}
            </Stack>
            {totalRounds > 0 && (
                <Box className={styles.roundCounter}>
                    {round.order + 1}/{totalRounds}
                </Box>
            )}
        </>
    );
}

const sxStyles = {
    fullCover: {
        position: 'absolute',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
    },
    optionButton: {
        '& + &': {
            borderLeft: '1px dashed gray',
        },
    },
    hint: {
        backgroundColor: 'rgba(0,0,0,0.4)',
        display: 'inline-block',
        padding: '0.5rem',
        borderRadius: '999px',
        zIndex: 2,
    },
};
