// makes playing audio return a promise

import {Howl} from 'howler';

export function playAudio(file: string): Promise<void> {
    return new Promise<void>(resolve => {
        const audio = new Howl({
            src: [`${process.env.PUBLIC_URL}/${file}`],
            onend() {
                resolve();
            },
        });

        audio.play();
    });
}
