import {Box, BoxProps, ButtonBase, Stack} from '@mui/material';
import type {ReactElement} from 'react';
import React, {useMemo} from 'react';
import {Link} from 'react-router-dom';

import type {LessonConfigPick1SplitScreen} from './types';
import {LessonType} from './types';
import {LESSONS} from '../../sessions/lessons';
import {ALL_TASKS_BY_ID} from '../../sessions/tasks';
import type {ShapeType} from '../Common/Shape';
import {Shape} from '../Common/Shape';
import type {Task} from '../Pages/assignments';
import {TaskType} from '../Pages/assignments';
import type {SessionMetrics} from '../PracticeSession/types';

export interface SessionButtonProps {
    lessonId: string;
}

type TaskTileProps = BoxProps & {
    task: Task;
};

function TaskTile({task, ...props}: TaskTileProps) {
    return (
        <Box
            {...props}
            flex={1}
            key={task.id}
            sx={{
                backgroundColor:
                    task.type === TaskType.Color ? task.value : 'white',
            }}
        >
            {task.type === TaskType.Shape && (
                <Shape shape={task.id as ShapeType} color="black" />
            )}
        </Box>
    );
}

export interface Pick1ButtonProps {
    tasks: Task[];
}

function Pick1Button({tasks}: Pick1ButtonProps) {
    return (
        <Stack height="100%" width="100%" direction="row">
            {tasks.map(t => (
                <TaskTile key={t.id} task={t} />
            ))}
        </Stack>
    );
}

export interface Pick1SplitScreenButtonProps {
    lesson: LessonConfigPick1SplitScreen;
    tasks: Task[];
}

function Pick1SplitScreenButton({
    lesson: {split},
    tasks,
}: Pick1SplitScreenButtonProps) {
    return (
        <Stack height="100%" width="100%" direction="column">
            <TaskTile task={tasks[0]} maxHeight="50%" />
            <Stack flex={1} direction="row" maxHeight="50%">
                {tasks.slice(1).map(t => (
                    <TaskTile key={t.id} task={t} />
                ))}
            </Stack>
        </Stack>
    );
}

export function LessonButton({lessonId}: SessionButtonProps): ReactElement {
    const records = useMemo<SessionMetrics[]>(() => {
        const json = localStorage.getItem(`lesson-${lessonId}.sessions`);
        return json ? JSON.parse(json) : [];
    }, [lessonId]);

    const score = records.length;

    const lesson = LESSONS[lessonId];

    const tasks = useMemo(
        () => lesson?.tasks.map(t => ALL_TASKS_BY_ID[t] as Task),
        [lesson?.tasks],
    );

    if (!lesson) {
        return <Box component="span">Unknown lesson: {lessonId}</Box>;
    }

    return (
        <Stack direction="row" alignItems="center">
            <Stack direction="column" alignItems="center">
                <ButtonBase
                    component={Link}
                    to={`/session/${lessonId}`}
                    sx={styles.button}
                >
                    {lesson.type === LessonType.Pick1 && (
                        <Pick1Button tasks={tasks} />
                    )}
                    {lesson.type === LessonType.Pick1SplitScreen && (
                        <Pick1SplitScreenButton lesson={lesson} tasks={tasks} />
                    )}
                </ButtonBase>
                <Box mt={0.5}>{score > 0 ? score : '-'}</Box>
            </Stack>
        </Stack>
    );
}

const styles = {
    button: {
        borderRadius: '1rem',
        width: '5rem',
        height: '5rem',
        transition: 'all .3s ease-out',
        ':hover': {
            transform: 'scale(1.1)',
        },
        boxShadow: '0 3px 6px rgba(0,0,0,0.5)',
        overflow: 'hidden',
    },
};
